.tabs {
    font-size: 30px;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.tab-list {
    display: flex;
    font-weight: bold;
    justify-content: flex-start;
    max-width: 100%;
    margin-left: 7.5em;
    list-style-type: none;
}


.tab-list li {
    cursor: pointer;
    // margin-right: 30px;
}


.tab-list-itemtab-list-active {
    text-decoration: underline;
    margin-left: 20px;
}

.tab-list-item {
    margin-left: 20px;
    // border-bottom: 2px solid black;
}



.tab-content {
    display: flex;
    flex-wrap: wrap;
    margin-top: 50px;
    margin-left: 10em;
}